:root {
  --gris: #49545e;
  --black: #0f1419;
  --sans: "Roboto Mono", monospace;
  --light: 100;
  --regular: 400;
  --bold: 700;
}

.sans {
  font-family: "Roboto Mono", monospace;
  font-family: var(--sans);
  letter-spacing: 0rem;
}

/*
* Typebase
* Mod
*/

/* Table of contents
––––––––––––––––––––––––––––––––––––––––––––––––––

- Google Fonts Imports
- Setup
- Copy & Lists
- Headings
- Tables
- CodeBlocks
- Spacing
- Utils
- Weights
- Especiales general
- Max width
*/

/* Setup
–––––––––––––––––––––––––––––––––––––––––––––––––– */

html {
  /* Default typefaces here */
  font-family: "Roboto Mono", monospace;
  font-family: var(--sans);
  font-size: 16px;
  font-size: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
}

/* Copy & Lists
–––––––––––––––––––––––––––––––––––––––––––––––––– */

p,
a {
  line-height: 1.755rem;
  margin-top: 1.7rem;
  margin-bottom: 0;
  letter-spacing: -0.001rem;
  font-weight: 400;
  font-weight: var(--regular);
}

ul,
ol {
  margin-top: 1.7rem;
  margin-bottom: 1.7rem;
}

ul li,
ol li {
  line-height: 1.7rem;
}

ul ul,
ol ul,
ul ol,
ol ol {
  margin-top: 0;
  margin-bottom: 0;
}

blockquote {
  line-height: 1.7rem;
  margin-top: 1.7rem;
  margin-bottom: 1.7rem;
}

/* Links
–––––––––––––––––––––––––––––––––––––––––––––––––– */

a {
  color: inherit;
  text-decoration: inherit;
}

/* Headings
–––––––––––––––––––––––––––––––––––––––––––––––––– */

h1,
h2,
h3,
h4,
h5,
h6 {
  /* Change heading typefaces here */
  font-weight: 400;
  font-weight: var(--regular);
  margin-top: 1.7rem;
  margin-bottom: 0;
  line-height: 1.7rem;
}

/* Sizes */

.xxxsmall {
  font-size: 62.5%;
  line-height: 1.5;
}

.xxsmall {
  font-size: 68.75%;
  line-height: 1.5;
}

.xsmall {
  font-size: 75%;
  line-height: 1.5;
}

h6,
.small {
  font-size: 87.5%;
  line-height: 1.5;
  letter-spacing: 0.04rem;
}

h5,
.root {
  font-size: 100%;
  line-height: 1.755rem;
  letter-spacing: 0.02rem;
}

h4,
.medium {
  font-size: 106.25%;
  line-height: 1.7;
  letter-spacing: -0.008rem;
}

h3,
.large {
  font-size: 150%;
  line-height: 1.4;
  letter-spacing: -0.012rem;
}

h2,
.xlarge {
  font-size: 200%;
  line-height: 1.3;
  letter-spacing: -0.02rem;
}

h1,
.xxlarge {
  font-size: 250%;
  line-height: 1.2;
  letter-spacing: -0.021rem;
}

.xxxlarge {
  font-size: 300%;
  line-height: 1.2;
}

.xxxxlarge {
  font-size: 400%;
  line-height: 1.2;
}

/* Tables
–––––––––––––––––––––––––––––––––––––––––––––––––– */

table {
  margin-top: 1.7rem;
  border-spacing: 0px;
  border-collapse: collapse;
}

table td,
table th {
  padding: 0;
  line-height: 33px;
}

/* Code-Blocks
–––––––––––––––––––––––––––––––––––––––––––––––––– */

code {
  vertical-align: bottom;
}

/* Utils
–––––––––––––––––––––––––––––––––––––––––––––––––– */

/* Hug the block above you */

.hug {
  margin-top: 0;
}

.hug1 {
  margin-top: 0.5rem;
}

/* No margin - no padding */

.p0 {
  padding: 0;
}

.m0 {
  margin: 0;
}

.pm {
  margin: 0;
  padding: 0;
}

/* TextAlign */

.center {
  text-align: center;
}

.right {
  text-align: right;
}

.left {
  text-align: left;
}

/* Opacity */

.op5 {
  opacity: 0.5;
}

.op6 {
  opacity: 0.6;
}

.op7 {
  opacity: 0.7;
}

.op8 {
  opacity: 0.8;
}

.op9 {
  opacity: 0.9;
}

.small {
  font-size: 80%;
}

/* Lineheight */

.l1 {
  line-height: 1rem;
}
.l11 {
  line-height: 1.1rem;
}
.l12 {
  line-height: 1.2rem;
}
.l13 {
  line-height: 1.3rem;
}
.l14 {
  line-height: 1.4rem;
}
.l1 {
  line-height: 1.5rem;
}

/* Weights
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.light {
  font-weight: 100;
  font-weight: var(--light);
}

.regular {
  font-weight: 400;
  font-weight: var(--regular);
}

strong,
.bold {
  font-weight: 700;
  font-weight: var(--bold);
  letter-spacing: 0.02rem;
}

/* Uppercase */

.up {
  text-transform: uppercase;
  letter-spacing: 0.04rem;
}

.gris {
  color: #49545e;
  color: var(--gris);
}

.white {
  color: white;
}

.exp {
  letter-spacing: 0.2em;
}

.shadow {
  text-shadow: 1px 1px 16px rgba(0, 0, 0, 0.5);
}

.shadow1 {
  text-shadow: 1px 1px 16px rgba(0, 0, 0, 1);
}

.shadowBlack {
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 1);
}

.balance-text {
  margin: 0 auto;
}

/* Span */

.bgBlack {
  background: black;
  color: white;
  padding: 0.1rem 0.4rem 0.2rem 0.4rem;
  margin: 0.1rem;
  word-break: none;
}

/* List */
.peace {
  list-style: none;
  padding-left: 0;
}

/* Especiales General
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.lead {
  font-size: 120%;
  line-height: 1.9rem;
}

figcaption {
  color: #49545e;
  color: var(--gris);
  margin: 0.3rem 0 0 0;
  font-size: 0.8rem;
  line-height: 1.4rem;
  font-weight: 400;
  font-weight: var(--regular);
}

figcaption strong {
  color: #0f1419;
  color: var(--black);
  font-weight: 400;
  font-weight: var(--regular);
}

/* Columns */

.columns {
  -webkit-column-count: 1;
     -moz-column-count: 1;
          column-count: 1;
  grid-column-gap: 3.5rem;
  -webkit-column-gap: 3.5rem;
     -moz-column-gap: 3.5rem;
          column-gap: 3.5rem;
  margin-top: 2em;
}

.columns > p:first-child {
  margin-top: 0;
}

/* GRANDE */
@media (min-width: 568px) {
  .columns {
    -webkit-column-count: 2;
       -moz-column-count: 2;
            column-count: 2;
  }
}

/* Media queries
–––––––––––––––––––––––––––––––––––––––––––––––––– */

@media (min-width: 740px) {
  html {
    font-size: 110%;
  }
}

/* Max width
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.max {
  max-width: 740px;
  margin: 0 auto;
  padding: 1rem;
}

.maxImg {
  max-width: 1160px;
}

/* GRAFICO */

.fuente {
  line-height: 1.1rem;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  font-family: "Roboto Mono", monospace;
}

body {
  font-family: sans-serif;
  position: relative;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  background: RGBA(9, 11, 15, 1);
  color: white;
}

.links {
  padding: 0.5em 0;
  border-bottom: 1px solid RGBA(255, 255, 255, 0.1);
}

button,
input {
  padding: 1em;
  background: white;
  color: black;
  border: 1px solid black;
}

button {
  cursor: pointer;
  opacity: 0.9;
}

button:hover {
  opacity: 1;
}

.linkBullet {
  padding-left: 1.5em !important;
}

.linkBullet:before {
  display: block;
  width: 10px;
  height: 10px;
  content: "";
  background: RGBA(28, 30, 32, 1);
  -webkit-transform: translate(-1.5em, 190%);
          transform: translate(-1.5em, 190%);
}

*:focus {
  outline: none;
}

@-webkit-keyframes pulse {
  from {
    opacity: 0.6;
  }

  70% {
    opacity: 1;
  }

  to {
    opacity: 0.6;
  }
}

@keyframes pulse {
  from {
    opacity: 0.6;
  }

  70% {
    opacity: 1;
  }

  to {
    opacity: 0.6;
  }
}

.buttonClear {
  background: white;
  -webkit-animation: pulse 800ms infinite;
          animation: pulse 800ms infinite;
}

.buttonCleared {
  background: RGBA(9, 11, 14, 0.2);
  color: RGBA(117, 117, 117, 1);
}

@media (max-width: 576px) {
  .footerFlex {
    max-width: 240px;
    margin: 0 auto;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

  .footerFlex > a {
    margin: 0 auto;
    text-align: center;
  }
}

:root {
  --color: RGBA(195, 194, 193, 0.1);
  --colorLight: RGBA(195, 194, 193, 0.1);
}

.link {
  color: inherit;
  text-decoration: none;
  /* margin: 0 .2em 0 .2em; */
}

.link {
  background-image: -webkit-gradient(linear, left top, left bottom, from(RGBA(195, 194, 193, 0.1)), to(RGBA(195, 194, 193, 0.1)));
  background-image: linear-gradient(RGBA(195, 194, 193, 0.1), RGBA(195, 194, 193, 0.1));
  background-image: -webkit-gradient(linear, left top, left bottom, from(var(--colorLight)), to(var(--colorLight)));
  background-image: linear-gradient(var(--colorLight), var(--colorLight));
  background-repeat: repeat-y;
  background-size: 0 0;
  background-position: 0 65%;
  -webkit-transition: 200ms ease;
  transition: 200ms ease;
}

.link:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(RGBA(195, 194, 193, 0.1)), to(RGBA(195, 194, 193, 0.1)));
  background-image: linear-gradient(RGBA(195, 194, 193, 0.1) 0%, RGBA(195, 194, 193, 0.1) 100%);
  background-image: -webkit-gradient(linear, left top, left bottom, from(var(--color)), to(var(--color)));
  background-image: linear-gradient(var(--color) 0%, var(--color) 100%);
  background-size: 100% 1em;
}

a {
  color: inherit;
  text-decoration: none;
  word-wrap: break-word;
